<template>
    <div class="helper">
        <span class="left">{{unFinishedTodoLength}} items left</span>
        <span class="clear" @click="clearAllCompleted()">Clear Completed</span>
    </div>
</template>
<script>
export default {
  metaInfo: {
    title: 'Todo-List'
  },
  props: {
    filter: {
      type: String,
      required: true
    },
    todos: {
      type: Array,
      required: true
    }
  },
  methods: {
    clearAllCompleted () {
      this.$emit('clearAllCompleted')
    }
  },
  computed: {
    unFinishedTodoLength () {
      return this.todos.filter(_ => !_.completed).length
    }
  }
}
</script>
<style scoped>
.helper{
    font-weight: 100;
    display: flex;
    justify-content: space-between;
    padding: 5px 0;
    line-height: 30px;
    background-color: #ffffff;
    font-size: 14px;
    font-smoothing: antialiased;
}
.left,.clear,.tabs{
    padding: 0px 10px;
    box-sizing: border-box;
}
.left,.clear{
    width: 150px;
}
.left{
    text-align: left;
}
.clear {
    text-align: right;
    cursor: pointer;
}
.tabs{
    width: 200px;
    display: flex;
    justify-content: space-around;
    & > *{
        display: inline-block;
        padding: 0px 10px;
        cursor: pointer;
        border: 1px solid rgba(175,47,47,0);
        &.actived{
            border-color: rgba(175,47,47,0.4);
            border-radius: 5px;
        }
    }
}
</style>
